
import React, { PropTypes } from 'react';

const commonPropTypes = {
  online: PropTypes.func.isRequired,
  login: PropTypes.func.isRequired,
  logout: PropTypes.func.isRequired,
  network: PropTypes.string.isRequired
};

const InOrOut = ({ online, login, logout, network }) => {
  if (online()) {
    return (
      <div>
        <button onClick={logout}>Logout</button>
      </div>
    );
  }

  return (
    <div>
      <p>Login to {network}</p>
      <button onClick={login}>Login</button>
    </div>
  );
};

InOrOut.propTypes = commonPropTypes;

const style = {
  marginTop: '20px',
  paddingBottom: '20px',
  // border: '2px solid red'
};

const OAuthComponent = ({ online, login, logout, network }) => (
  <div style={style}>
    <h1>hello {network}</h1>

    <InOrOut
      online={online}
      login={login}
      logout={logout}
      network={network}
    />
  </div>
);

OAuthComponent.propTypes = commonPropTypes;

export default OAuthComponent;



