
import React from 'react';
import * as hello from 'hellojs';

import OAuthComponent from '../components/oauth';
import meetup, { meetupInit } from './meetup';

const NETWORK = meetup;
const CLIENT_ID = '7far32sob13unf2ga5qu8m3275';
const initArgs = [{ [NETWORK]: CLIENT_ID }];
// const loginArgs = {};
// const loginArgs = { force: true };

// const NETWORK = 'google';
// const CLIENT_ID = '731569769415-4tfi5mh7nvcut9lo2ilf50ob8ahivlv6.apps.googleusercontent.com';
// const initArgs = [
//   { [NETWORK]: CLIENT_ID },
//   { redirect_uri: 'http://localhost:3000/' }
// ];
// const loginArgs = {};

const online = () => {
  const session = hello.getAuthResponse(NETWORK);
  const currentTime = (new Date()).getTime() / 1000;
  const isOnline = session && session.access_token && session.expires > currentTime;
  return isOnline;
};

export default class OAuthContainer extends React.Component {
  constructor(props) {
    super(props);

    // hello.init(...initArgs);

    this.state = {
      network: NETWORK,
      session: hello.getAuthResponse(NETWORK)
    };
    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
  }

  login() {
    meetupInit(hello);

    hello.init(...initArgs);

    hello.login(NETWORK, { force: false }).then(() => {
      const session = hello.getAuthResponse(NETWORK);
      console.log({ loginOK: session });

      this.setState({ session });
    }, (err) => {
      console.error(err);
    });

    // hello.login(NETWORK, loginArgs).then(() => {
    //   const session = hello.getAuthResponse(NETWORK);
    //   console.log({ loginOK: session });

    //   this.setState({ session });
    // }, (err) => {
    //   console.error(err);
    // });
  }

  logout() {
    hello.logout(NETWORK).then(() => {
      console.log('Logged out successfully');

      this.setState({ session: null });
    }, (err) => {
      console.error(err);
    });
  }

  render() {
    return (
      <OAuthComponent
        online={online}
        login={this.login}
        logout={this.logout}
        network={this.state.network}
      />
    );
  }
}



