
import React from 'react';

import logo from './logo.svg';
import './App.css';

import OAuth from './containers/oauth';

export default () => (
  <div className="App">
    <div className="App-header">
      <img src={logo} className="App-logo" alt="logo" />
      <h2>Welcome to OAuth Testing</h2>
    </div>

    <OAuth />
  </div>
);



